import {
	mapState,
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			id: '',
			ac_des: '',
			ac_sort: '',
			ac_name: '',
			cate: [],
			store_desc: '',
			store_phone: '',
			wtime_start: '',
			wtime_end: '',
			store_map_address_detail: '',
			flist: [],
			selectflist: []
		}
	},
	computed: mapState(['sellerHasLogin']),
	onLoad() {
		console.log('页面加载');
		this.loadData();
	},
	methods: {
		//...mapMutations(['logout','setUserInfo']),  
		async loadData() {
			if (!this.sellerHasLogin) {
				uni.navigateTo({
					url: 'login',
				});
			} else {
				
			}
		},
		toPost() {
			var _this = this;
			let postData = {
				name: this.ac_name,
				description: this.ac_des,
				sort: this.ac_sort,
			}
			console.log(postData)
			this.$Request.post('photos/album_add_save', postData).then(res => {
				if (res.code == 200) {
					console.log(res);
					uni.showToast({
						icon: 'none',
						title: '添加成功！',
						duration: 2000
					});
					_this.loadData();
					setTimeout(function() {
						uni.redirectTo({
							url: "/pages/seller/set-photo"
						});
					}, 1500)
				} else {
					uni.showToast({
						icon: 'none',
						title: res.datas.error,
						duration: 2000
					});
					
				}
			});
		},
		toPage: function(url, id) {
			uni.navigateTo({
				url: url + '?id=' + id,
			})
		}

	}
}
